#game::after {
  content: "";
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: block;
  background: -webkit-radial-gradient(rgba(0, 0, 0, 0), #000000);
  background: -o-radial-gradient(rgba(0, 0, 0, 0), #000000);
  background: radial-gradient(rgba(0, 0, 0, 0), #000000);
}

.Game>#container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  text-align: center;
}
.Game>#container>canvas {
  max-width: 100%;
  max-height: 100%;
}

.Game>#vv {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  text-align: center;
}

.Game>#centerTip {
  position: absolute;
  width: 100vw;
  top: 48vh;
  text-align: center;
}



